.content__welcome {
  display: flex;
  gap: 16px;
  align-items: center;

  h1 {
    font-size: 24px;
    line-height: 100%;
    color: #3389f1;
  }
  img {
    height: 64px;
    transition: .25s;

    &:hover {
      transform: scale(1.1) rotate(-8deg);
    }
  }
}

//Cards
.Home {
  .Card {
    flex: 0 1 calc(25% - 24px);
  }
}