.content__welcome {
  display: flex;
  gap: 16px;
  align-items: center;
}
.content__welcome h1 {
  font-size: 24px;
  line-height: 100%;
  color: #3389f1;
}
.content__welcome img {
  height: 64px;
  transition: 0.25s;
}
.content__welcome img:hover {
  transform: scale(1.1) rotate(-8deg);
}

.Home .Card {
  flex: 0 1 calc(25% - 24px);
}

